
.all-tabs{
    background-color: white;
    width: 100%;
    text-align: center;
    padding: 0%;
    border-bottom: solid rgb(167, 172, 188);
    box-shadow: 5px 10px 18px #b1afaf80;
    position: sticky;
  }
  ul{
    display: flex;
    width: 80%;
    padding: 0%;
    margin:0%;
    margin-right: auto;
    margin-left: auto;
    
  }
  li,a{
    text-decoration: none;
    list-style-type: none;
    padding:10px;
    width: 100%;
    cursor: pointer;
    font-size: 18px;
  } 
 
  Link{
    text-decoration: none;
  }
  .body{
    display: table;
      margin: auto;
      width:80%;
      text-align: center;
      margin-top: 10%;
  }
  .book-search{
    display: block;
  }
  .add-input,.shaw-data{
    display: block;
    width: 50%;
    margin: auto;
    text-align: center;
}
button{
    width: 150px;
    padding:15px;
    border-radius: 5px;
    border:none;
    background-color: cadetblue;
    margin: 1%;
    cursor: pointer;
    transition-duration: 0.4s;
    outline: none;
}
button:hover{
    background-color: rgba(95, 158, 160, 0.904);
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

input{
    padding: 15px;
    border-radius: 5px;
    background-color: rgb(236, 236, 236);
    border: none;
    margin: 3%;
}
.nav{
  color: rgb(167, 172, 188);
}
.active-nav{
  display: flexbox;
  color:rgb(94, 108, 157);
  border-bottom: solid rgb(94, 108, 157);

}

